import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import dnIcon from '../../components/dn-icon/dn-icon.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			photoAlbumDto: {
				cover: '',
				introduction: '',
				name: '',
				type: ''
			},
			status:true,
			userDto: {
			},
			friend: '',
			photo: '',
			journal: '',
			journalList: [],
			juEs: [],
			bannercom: {},
			TabCur: 0,
			tabList: [{ name: '日志' }, { name: '相册' }, { name: '音乐' }],
			pictureNum: []
		};
	},
	components: {
		dnIcon,
		WucTab,
		uniPopup
	},
	methods: {
		change(){
			this.status =! this.status;
		},
		getPictureNum() {
			var _self = this;
			uni.request({
				url: this.$baseUrl + '/photoalbum/all',
				method: 'POST',
				data: {
					id: this.userDto.id
				},
				success: res => {
					console.log(res.data.data);
					this.pictureNum = res.data.data;
				}
			});
		},
		creatPhotoalbum(photoAlbumDto) {
			uni.request({
				url: this.$baseUrl + '/photoalbum/apa',
				method: 'POST',
				data: {
					userId: this.userDto.id
				},
				success: res => {
					if (res.data.msg === '成功') {
						uni.showToast({
							title: res.data.msg
						});
					} else {
						uni.showToast({
							title: res.data.msg
						});
					}
				}
			});
		},
		getJuEs() {
			var _self = this;
			uni.request({
				url: this.$baseUrl + '/journal/user/data',
				method: 'POST',
				data: {
					id: this.userDto.id
				},
				success: res => {
					console.log(res.data.data);
					this.juEs = res.data.data;
				}
			});
		},
		tabChange(index) {
			this.TabCur = index;
		},
		swiperChange(e) {
			let { current } = e.target;
			this.TabCur = current;
		},
		open() {
			// 需要在 popup 组件，指定 ref 为 popup
			this.$refs.popup.open();
		},
		goDetail(item) {
			let detail = {
				id: item.id,
				userId: item.userId,
				thumbnail: item.thumbnail,
				createTime: item.createTime,
				comments: item.comments,
				title: item.title,
				avatar: item.avatar,
				nickname: item.nickname,
				likes: item.likes,
				content: item.content
			};
			uni.navigateTo({
				url: '../myj_detail/myj_detail?detailDate=' + encodeURIComponent(JSON.stringify(detail))
			});
		},
		userSum: function(userDto) {
			uni.request({
				url: this.$baseUrl + '/user/sum',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					id: userDto.id
				},
				success: res => {
					if (res.data.code === 1) {
						console.log(res.data.data.friendSum);
						this.friend = res.data.data.friendSum;
						console.log(this.friend);
						this.photo = res.data.data.photoAlbumSum;
						this.journal = res.data.data.journalSum;
					}
				}
			});
		},
		deleteAlbum(id,index){
			this.pictureNum.splice(index,1,false)
			uni.request({
				url:this.$baseUrl+'/photoalbum/dpa',
				method:'DELETE',
				data:{
					id:id,
					userId:this.userDto.id,
				},
				success: res=>{
					console.log('aaa')
					console.log(res.data.msg)
				}
			})
			
		},
		updateAlbum(id,name){
			uni.request({
				url:this.$baseUrl+'/photoalbum/upa',
				method:'PUT',
				data:{
					id:id,
					name:name,
					userId:this.userDto.id,
				},
				success:res =>{
					console.log(res.data.msg)
					}
			})
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.userDto = res.data;
				console.log(that.userDto);
				that.userSum(that.userDto);
				that.getJuEs();
				that.getPictureNum();
			}
		});
	}
};
